/* ==========================
   Shared Base Section Style
============================= */
section {
  position: relative;
  width: 100vw;
  box-sizing: border-box;
  padding: 60px 40px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #e0e5ec;
  color: #000;
  margin-bottom: 80px;
}

/* ==========================
   ABOUT SECTION
============================= */
.about-section {
  max-width: calc(100vw - 220px);
  margin-left: 220px;
  padding: 60px 40px;
  min-height: 100vh;
  overflow: hidden;
  z-index: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-bottom: 60px;
}

.about-section::before {
  content: none;
}

.section-title {
  font-size: 2.8rem;
  font-weight: 700;
  color: #1e90ff;
  border-bottom: 4px solid #1e90ff;
  padding-bottom: 10px;
  margin-bottom: 30px;
}

.section-subtitle {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 30px;
  font-style: italic;
}

.about-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 900px;
  gap: 30px;
}

.about-text p {
  background-color: #e0e5ec;
  border-radius: 12px;
  padding: 20px 25px;
  font-size: 1.1rem;
  line-height: 1.7;
  color: #000;
  box-shadow: 8px 8px 16px #bec4d0,
              -8px -8px 16px #ffffff;
  text-align: left;
  width: 100%;
  box-sizing: border-box;
}

/* ==========================
   EDUCATION & CERTIFICATION
============================= */
.education-section,
.certification-section {
  background-color: #e0e5ec;
  border-radius: 12px;
  padding: 25px 30px;
  width: 100%;
  max-width: 900px;
  box-sizing: border-box;
  box-shadow: 8px 8px 16px #bec4d0,
              -8px -8px 16px #ffffff;
}

.education-title,
.certification-title {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #1e90ff;
  border-bottom: 2px solid #1e90ff;
  padding-bottom: 8px;
  text-align: center;
}

.education-list,
.certification-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.education-item,
.certification-item {
  text-align: left;
}

.education-item h4,
.certification-item h4 {
  font-size: 1.25rem;
  margin: 0 0 6px 0;
  color: #000;
}

.education-item p,
.certification-item p {
  font-size: 1rem;
  margin: 0;
  color: #333;
}

.certification-section {
  margin-bottom: 100px; /* Ensure spacing before contact section */
}

/* ==========================
   CONTACT INFO SECTION
============================= */
.contact-info-section {
  position: relative;
  width: 100vw;
  min-height: 100vh;
  margin-left: 0;
  padding: 40px 0;
  text-align: center;
  background-color: #e0e5ec;
  color: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;
  overflow-x: hidden;
  z-index: 1;
  margin-top: 60px;
}

.contact-info-section::before {
  content: none;
}

.section-title .highlight {
  color: #f94144;
}

.info-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  width: 100%;
  max-width: 1200px;
  margin-top: 20px;
}

.info-card {
  background-color: #e0e5ec;
  padding: 25px 20px;
  width: 260px;
  border-radius: 16px;
  box-shadow: 8px 8px 16px #bec4d0,
              -8px -8px 16px #ffffff;
  transition: all 0.3s ease;
  color: #000;
  text-align: center;
  cursor: pointer;
  margin-bottom: 20px;
  min-height: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.info-card:hover {
  transform: translateY(-6px);
  box-shadow: inset 4px 4px 10px #c1c6d4,
              inset -4px -4px 10px #ffffff;
}

.info-icon {
  width: 50px;
  margin-bottom: 15px;
  filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.15));
}

/* ==========================
   RESPONSIVE STYLES
============================= */
@media (max-width: 768px) {
  .about-section,
  .contact-info-section {
    margin-left: 0;
    max-width: 100vw;
    padding: 40px 20px;
    margin-bottom: 60px;
  }

  .section-title {
    font-size: 2.2rem;
  }

  .education-title,
  .certification-title {
    font-size: 1.6rem;
  }

  .education-item h4,
  .certification-item h4 {
    font-size: 1.1rem;
  }

  .education-item p,
  .certification-item p {
    font-size: 0.95rem;
  }

  .info-card {
    width: 90%;
  }
}

@media (max-width: 900px) {
  .info-cards {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .info-card {
    width: 90%;
    margin-bottom: 20px;
    min-height: 160px;
  }
}

@media (min-width: 769px) {
  .about-section,
  .contact-info-section,
  .certification-section {
    margin-left: 220px;
    max-width: calc(100vw - 220px);
  }
}
